type Props = {
    routes: RouteMenuItem[]
}

export default function AdminApp(props: Props) {

    const classes = useStyles()
    const theme = useTheme()
    const [openSideMenu, setOpenSideMenu] = useState(true)
    const [openLogin, setOpenLogin] = useState(false)
    const [isLoggedIn, setIsLoggedIn] = useState(false)

    const handleDrawerOpen = () => {
        setOpenSideMenu(true)
    }

    const handleDrawerClose = () => {
        setOpenSideMenu(false)
    }

    const autoLogin = () => {
        adminApi.auth.loadLoginCache()
        if (adminApi.auth.user.id > 0) {
            console.log('adminApi.auth.user : ', adminApi.auth.user)
            adminApi.auth
                .isLoggedIn()
                .then(resData => {
                    setIsLoggedIn(resData)
                })
                .catch(e => {
                    console.log(e)
                })
        }
        else
            adminApi.auth.setLoggedOut()
    }

    useEffect(() => {
        // adminApi.auth.autoLogin()
        //
        // const loggedIn = adminApi.auth.isLoggedIn()
        //
        // // console.log('isLoggedIn: ', adminApi.auth.isLoggedIn)
        // // console.log('setLoggedOut: ', adminApi.auth.setLoggedOut)
        //
        // setIsLoggedIn(loggedIn)
        // console.log('isLoggedIn: ', loggedIn)

        autoLogin()
    }, [])

    useEffect(() => {

        setOpenLogin(isLoggedIn === false)

        if (isLoggedIn) {
        } else {
            //console.log('로그인되지 않았음.')
        }
    }, [isLoggedIn])

    return (
        <div className={classes.root}>
            <BrowserRouter>
                {/*<AppBar position="fixed" className={clsx(classes.appBar, { [classes.appBarShift]: openSideMenu })}>*/}
                {/*    <Toolbar variant="dense">*/}
                {/*        <IconButton color="inherit" aria-label="open drawer" onClick={handleDrawerOpen}*/}
                {/*                    edge="start" className={clsx(classes.menuButton, openSideMenu && classes.hide)}>*/}
                {/*            <MenuIcon />*/}
                {/*        </IconButton>*/}
                {/*        <Typography variant="h6" noWrap component="div">Admin Gosu</Typography>*/}
                {/*    </Toolbar>*/}
                {/*</AppBar>*/}

                <AppHeader openSideMenu={openSideMenu} handleDrawerOpen={handleDrawerOpen}/>

                <Drawer className={classes.drawer} variant="persistent" anchor="left"
                        open={openSideMenu} classes={{ paper: classes.drawerPaper }}>

                    <div className={classes.drawerHeader}>
                        <IconButton onClick={handleDrawerClose}>
                            {theme.direction === "ltr" ? <ChevronLeftIcon /> : <ChevronRightIcon />}
                        </IconButton>
                    </div>
                    <Divider />

                    {
                        isLoggedIn &&
                        props.routes.map((elem, index) => {
                            const { title, path, icon, role } = elem
                            return (
                                <div key={index + 1}>
                                    {path === "header" ? <Divider /> : null}

                                    <ListItem button component={NavLink} to={path} key={index + 1}>
                                        {icon && <ListItemIcon style={{minWidth: "40px"}}>{icon}</ListItemIcon>}
                                        {/*<ListItemText classes={{ primary: classes.listItemText }} primary={title} />*/}
                                        {/*<ListItemText className={classes.listItemText} primary={title} />*/}
                                        <ListItemText style={{fontSize: "0.5rem"}} primary={title} />
                                    </ListItem>
                                </div>
                            )
                        })
                    }
                </Drawer>

                <main className={clsx(classes.content, { [classes.contentShift]: openSideMenu })}>
                    <div className={classes.drawerHeader} />

                    <Switch>
                        <Route exact path="/" component={Home} />
                        {/*<Route exact path="/login" component={LogInPage} />*/}
                        <Route path={"/users"} component={UserList} />
                        <Route path={"/admin-users"} component={AdminUserList} />
                        <Route path={"/videos"} component={VideoList} />
                        <Route path={"/banners"} component={Banners} />
                        <Route path={"/notices"} component={NoticeList} />

                        <Route exact path="/ztest" component={ZTestPage} />
                        {/*<Route exact path="/test-app" component={TestAppFrontend} />*/}

                        <Route component={PageNotFound} />
                    </Switch>

                    <LoginPopup open={openLogin} setOpen={setOpenLogin}/>
                </main>
            </BrowserRouter>
        </div>
    )
}
