import React from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { Paper, Button, TextField } from '@material-ui/core'
import { InputsVideo } from '../pages/Videos/VideoForm'
import { InputsLogIn } from './LogInForm'
import { AdminUserApi } from '../services/adminApi'
import { LoginDto } from '../services/dto/user.dto'


export type InputsRegister = {
    userid: string
    username: string
    password: string
    passwordConfirm: string
    secretCode: string
}

type Props = {
    handleRegister: Function
    //onHandleSuccess?: () => void
}

export default function RegisterForm(props: Props) {

    const { register, handleSubmit, reset, watch, errors } = useForm<InputsRegister>()

    const onSubmit: SubmitHandler<InputsRegister> = data => {
        // console.log('onSubmit: ', data)
        console.log('Boolean(data.secretCode): ', Boolean(data.secretCode))

        if (data.password.length < 4) {
            alert('4자리 이상의 비밀번호를 입력하십시오.')
            return
        } else if (data.password !== data.passwordConfirm) {
            alert('비밀번호가 일치하지 않습니다.')
            return
        } else if (Boolean(data.secretCode) === false) {
            alert('유효한 시크릿코드를 넣으십시오.')
            return
        }

        props.handleRegister(data.userid, data.password, data.username, data.secretCode)

        reset()
    }


    return (

        <form className='form' onSubmit={handleSubmit(onSubmit)}>
            <TextField margin='dense' id='userid' name='userid' label='이메일' type='text'
                       fullWidth variant='standard' autoFocus inputRef={register({ required: true })} />

            <TextField margin='dense' id='username' name='username' label='이름' type='text'
                       fullWidth variant='standard' inputRef={register({ required: true })} />

            <TextField margin='dense' id='password' name='password' label='비밀번호' type='password'
                       fullWidth variant='standard' inputRef={register({ required: true })} />
            <TextField margin='dense' id='passwordConfirm' name='passwordConfirm' label='비밀번호 확인' type='password'
                       fullWidth variant='standard' inputRef={register({ required: true })} />

            <TextField margin='dense' id='secretCode' name='secretCode' label='시크릿코드' type='text'
                       fullWidth variant='standard' inputRef={register({ required: true })} />

            <Button type='submit' variant='outlined' color='primary' style={{ marginTop: '16px' }}>회원가입</Button>
        </form>

    )
}
