import ListItemText from '@material-ui/core/ListItemText'
import ListItem from '@material-ui/core/ListItem'
import List from '@material-ui/core/List'
import Divider from '@material-ui/core/Divider'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import CloseIcon from '@material-ui/icons/Close'
import Slide from '@material-ui/core/Slide'
import { TransitionProps } from '@material-ui/core/transitions'
import { LoginDto } from '../../services/dto/user.dto'
import { AdminUserApi } from '../../services/adminApi'
import * as adminApi from '../../services/adminApi'
import { Grid, Paper } from '@material-ui/core'
import LogInForm from '../../components/LogInForm'
import RegisterForm from '../../components/RegisterForm'
import { CreateAdminUserDto } from '../../services/dto/adm-user.dto'
import Jumbotron from '../../components/Jumbotron'
import { LoginResultCode, RegisterResultCode } from '../../services/dto/common-dto-defs'

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        appBar: {
            position: 'relative'
        },
        title: {
            marginLeft: theme.spacing(2),
            flex: 1
        },
        paper: {
            padding: theme.spacing(2),
            textAlign: 'center',
            color: theme.palette.text.secondary
        }
    })
)

const Transition = React.forwardRef(function Transition(
    props: TransitionProps & {
        children?: React.ReactElement;
    },
    ref: React.Ref<unknown>
) {
    return <Slide direction='up' ref={ref} {...props} />
})

type Props = {
    open: boolean
    setOpen: (open: boolean) => void
}

export default function LoginPopup(props: Props) {

    const classes = useStyles()
    //const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        props.setOpen(true)
    }

    const handleClose = () => {
        props.setOpen(false)
    }

    const login = async (userid: string, password: string) => {

        const dto = new LoginDto()
        dto.userid = userid
        dto.password = password

        AdminUserApi
            .login(dto)
            .then(resData => {
                console.log('resData: ', resData)

                switch (resData.resCode) {
                    case LoginResultCode.Success: {
                        const { id, userid, username, role, created } = Boolean(resData.user) ? resData.user : resData.member // resData.user
                        adminApi.auth.setLoggedIn(id, userid, username, 1)
                        window.location.reload()
                        break
                    }
                    case LoginResultCode.InternalServerError: {
                        alert('알 수 없는 서버 에러입니다.')
                        break
                    }
                    case LoginResultCode.NoUserFound: {
                        alert('해당 사용자가 존재하지 않습니다.')
                        break
                    }
                    case LoginResultCode.InvalidPassword: {
                        alert('비밀번호가 유효하지 않습니다.')
                        break
                    }
                }
            })
            .catch(e => {
                console.error(e)
                alert('로그인에 실패하였습니다')
            })
    }

    const registerUser = async (userid: string, password: string, username: string, secretCode: string) => {
        const dto = new CreateAdminUserDto()
        dto.assign(userid, password, username, secretCode)

        AdminUserApi
            .register(dto)
            .then(resData => {
                console.log('resData: ', resData)

                switch (resData.resCode) {
                    case RegisterResultCode.Success: {
                        alert('\n회원가입에 성공하였습니다.\n\n로그인을 하십시오.\n')
                        break
                    }
                    case RegisterResultCode.InternalServerError: {
                        alert('알 수 없는 서버 에러입니다.')
                        break
                    }
                    case RegisterResultCode.AlreadyExists: {
                        alert('이미 사용자가 존재합니다.')
                        break
                    }
                    case RegisterResultCode.InvalidSecretCode: {
                        alert('시크릿코드가 유효하지 않습니다.')
                        break
                    }
                }
            })
            .catch(e => {
                console.error(e)
                alert('회원가입이 되지 않았습니다.')
            })
    }

    // <Dialog fullScreen open={props.open} onClose={handleClose} TransitionComponent={Transition}>

    return (
        <div>


            <Dialog fullScreen open={props.open} onClose={handleClose}>
                <AppBar className={classes.appBar}>
                    <Toolbar>
                        {/*<IconButton edge='start' color='inherit' onClick={handleClose} aria-label='close'><CloseIcon /></IconButton>*/}

                        <Typography variant='h6' className={classes.title} component='div'>로그인</Typography>

                        {/*<Button autoFocus color='inherit' onClick={handleClose}>save</Button>*/}
                    </Toolbar>
                </AppBar>


                <Grid container spacing={3} style={{ marginTop: '20px' }}>
                    {/*<Grid item xs={12}>*/}
                    {/*    <Paper className={classes.paper}>xs=12</Paper>*/}
                    {/*</Grid>*/}
                    {/*<Grid item xs={6}>*/}
                    {/*    <Paper className={classes.paper}>xs=6</Paper>*/}
                    {/*</Grid>*/}
                    {/*<Grid item xs={6}>*/}
                    {/*    <Paper className={classes.paper}>xs=6</Paper>*/}
                    {/*</Grid>*/}

                    <Grid item xs={12}>
                        {/*<Jumbotron title={"제목 "} description={"내용 ----"} />*/}
                        <Typography>
                            관리자에게 *시크릿코드* 값을 받아 회원가입을 하십시오
                        </Typography>
                        <Typography>
                            <span>(현재 시크릿코드값: gs2021)</span>
                        </Typography>
                    </Grid>

                    <Grid item xs={2}></Grid>
                    <Grid item xs={4}>
                        <Paper className={classes.paper}>
                            <LogInForm handleLogin={login} />
                        </Paper>
                    </Grid>
                    {/*<Grid item xs={1}></Grid>*/}
                    <Grid item xs={4}>
                        <Paper className={classes.paper}>
                            <RegisterForm handleRegister={registerUser} />
                        </Paper>
                    </Grid>
                    <Grid item xs={2}></Grid>


                </Grid>
            </Dialog>
        </div>
    )
}
