import React, { useEffect, useState } from 'react'
import GenericTable from '../../parts_lib/GenericTable/GenericTable'
import { NoticeApi } from '../../services/adminApi'
import { AdmNoticeDto } from '../../services/dto/adm-notice.dto'
import NoticeForm from './NoticeForm'
import { VideoType } from '../../services/dto/common-dto-defs'

const tableHeader = {
    id: 'id',
    title: '제목',
    content: '본문',
    start_at: '시작일',
    end_at: '종료일'
}

const dummyRows = [
    {
        id: 2,
        title: '운전면허 도로주행시험',
        link: 'https://www.youtube.com/embed/eU0W2MctU8s'
    },
    {
        id: 4,
        title: '도로주행시험 안내 동영상',
        link: 'https://www.youtube.com/embed/wjcdWb-wHtc'
    }
]


export default function NoticeList() {
    //
    const [rows, setRows] = useState([])
    const [rowForUpdate, setRowForUpdate] = useState(null)
    const [openForm, setOpenForm] = useState(false)

    /// CRUD    ----------------------------------------------------------------
    const readAll = (page: number = 0, limit: number = 0) => {
        NoticeApi
            .readAll(page, limit)
            .then(resData => {
                console.log('NoticeApi.readAll : ', resData)
                for (let row of resData.rows) {
                    delete row.created
                    delete row.updated
                    row.start_at = row.start_at.split('T')[0]
                    row.end_at = row.end_at.split('T')[0]
                }
                setRows(resData.rows)
            })
            .catch(e => {
                console.error(e)
            })
    }

    const createOrUpdate = (dto: AdmNoticeDto) => {
        NoticeApi
            .createOrUpdate(dto)
            .then(resData => {
                console.log('NoticeApi.createOrUpdate : ', resData)
                readAll()
            })
            .catch(e => {
                console.error(e)
            })
            .finally(() => {
                setOpenForm(false)
                setRowForUpdate(null)
            })
    }

    const deleteMany = (idList: number[]) => {
        if (Boolean(idList) && idList.length > 0) {
            NoticeApi
                .deleteMany(idList)
                .then(resData => {
                    console.log('NoticeApi.deleteMany : ', resData)
                    readAll()
                })
                .catch(e => {
                    console.error(e)
                })
        }
    }

    /// CRUD End   -------------------------------------------------------------


    const onClickCreate = () => {
        setOpenForm(true)
    }

    const createOne = (dto: AdmNoticeDto) => {

    }

    const onClickUpdate = (item: any) => {
        setRowForUpdate(item)
        setOpenForm(true)
    }

    const onClickDelete = (selectedIDs: Set<any>) => {
        deleteMany(Array.from(selectedIDs))
    }

    useEffect(() => {
        readAll()
    }, [])

    return (
        <div>
            공지 사항

            <GenericTable items={rows} headers={tableHeader}
                          onClickCreate={onClickCreate}
                          onClickEdit={onClickUpdate}
                          onClickDelete={onClickDelete}
            />

            <NoticeForm open={openForm} setOpen={setOpenForm} title='공지사항 등록'
                        createRow={createOrUpdate}
                        updateRow={createOrUpdate}
                        rowForUpdate={rowForUpdate}
            />
        </div>
    )
}
