import * as React from 'react'
import clsx from 'clsx'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew'
import MenuIcon from '@material-ui/icons/Menu'
import Typography from '@material-ui/core/Typography'
import AppBar from '@material-ui/core/AppBar'
import { makeStyles, Theme } from '@material-ui/core/styles'
import { Badge, createStyles, Grid } from '@material-ui/core'
import { LoginDto } from '../services/dto/user.dto'
import { AdminUserApi, Auth } from '../services/adminApi'
import * as adminApi from '../services/adminApi'
import { useEffect, useState } from 'react'


const SIDE_MENU_WIDTH = 180

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        appBarRoot: {
            //flexGrow: 1,
        },
        appBar: {
            transition: theme.transitions.create(["margin", "width"], {
                easing: theme.transitions.easing.sharp,
                duration: theme.transitions.duration.leavingScreen
            })
        },
        appBarShift: {
            width: `calc(100% - ${SIDE_MENU_WIDTH}px)`,
            marginLeft: SIDE_MENU_WIDTH,
            transition: theme.transitions.create(["margin", "width"], {
                easing: theme.transitions.easing.easeOut,
                duration: theme.transitions.duration.enteringScreen
            })
        },
        menuButton: {
            marginRight: theme.spacing(2),
            color: "white"
        },
        hide: {
            display: "none"
        },
    })
)

type Props = {
    openSideMenu: boolean
    handleDrawerOpen: ()=>void
}

export default function AppHeader(props: Props) {
    //
    const classes = useStyles()

    const [username, setUsername] = useState('')

    const logout = () => {
        setUsername('')
        adminApi.auth.setLoggedOut()
        AdminUserApi
            .logout()
            .then(resData => {
                console.log('logout resData: ', resData)
                window.location.reload()
            })
            .catch(e => {
                console.error(e)
                alert('로그아웃 에러')
            })
    }

    useEffect(()=>{
        // setUsername(adminApi.auth.user.username)

        console.log('Appbar useEffect user: ', adminApi.auth.user)
        setTimeout(()=>{
            console.log('Appbar useEffect user: ', adminApi.auth.user)
            setUsername(adminApi.auth.user.username)
        }, 2000)
        // adminApi.auth.isLoggedIn()
        //     .then(res => {
        //         setUsername(adminApi.auth.user.username)
        //     })
    },[])

    return (
        <div className={classes.appBarRoot}>
            <AppBar position="fixed" className={clsx(classes.appBar, { [classes.appBarShift]: props.openSideMenu })}>
                <Toolbar variant="dense">
                    <IconButton color="inherit" aria-label="open drawer" onClick={props.handleDrawerOpen}
                                edge="start" className={clsx(classes.menuButton, props.openSideMenu && classes.hide)}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="subtitle1"   component="div">Gosu</Typography>

                    {/*<IconButton onClick={logout}><Badge><PowerSettingsNewIcon/></Badge></IconButton>*/}

                    <Grid container>
                        {/*<Grid item xs={3}>*/}
                        {/*</Grid>*/}
                        <Grid item xs={10}>

                        </Grid>
                        <Grid item xs={1}>
                            <IconButton color="secondary" style={{color:"white"}}><Badge><Typography>{username}</Typography></Badge></IconButton>
                        </Grid>
                        <Grid item xs={1}>
                            <IconButton onClick={logout} color="secondary" style={{color:"white"}}><Badge><Typography><small>Logout</small></Typography></Badge></IconButton>
                        </Grid>
                    </Grid>
                </Toolbar>
            </AppBar>
        </div>
    )
}
