import React, { ReactNode } from 'react'
import { makeStyles } from '@material-ui/core/styles'
import {IconButton} from '@material-ui/core'

const useStyles = makeStyles(theme => ({
    actionButton: {
        minWidth: 0,
        //margin: theme.spacing(0.5),
        // 안먹힌다.
        border: '2px solid #73AD21',
        borderRadius: '4px'
    },
}))

interface Props {
    color: "root" | "secondary" | "primary"
    onClick: () => void
    children: ReactNode // JSX.Element
    variant: 'text' | 'outlined' | 'contained'
}

export default function SmallIconButton(props: Props) {

    const classes = useStyles()
    const outlinedIconButtonStyle = {border: '2px solid #666666', borderRadius: '4px'}

    return (
        <IconButton size="small" color="primary" component="span"
                    className={`${classes.actionButton}`}
                    style={outlinedIconButtonStyle}
        >
            {/*<PhotoCamera />*/}
            {/*<EditOutlinedIcon fontSize='large'/>*/}
            {props.children}
        </IconButton>
    )
}