import * as React from 'react'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import TextField from '@material-ui/core/TextField'
import { Card, Divider, Typography } from '@material-ui/core'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import { makeStyles } from '@material-ui/core/styles'
import { SubmitHandler, useForm } from 'react-hook-form'
import { AdmNoticeDto } from '../../services/dto/adm-notice.dto'

//import DateFnsUtils from '@date-io/date-fns'
import AdapterDateFns from '@material-ui/lab/AdapterDateFns'
import LocalizationProvider from '@material-ui/lab/LocalizationProvider'
import DatePicker from '@material-ui/lab/DatePicker'
import { useEffect } from 'react'



const useStyles = makeStyles({
    cardRoot: {
        // display: 'flex',
        // flexGrow: 1,
        maxWidth: 345,
        // justifyContent: 'center'
        marginLeft: '18px'
    },
    cardMedia: {
        height: 140
        // display: 'flex',
        // justifyContent: 'center',
        // marginLeft: '20px'
    }
})

interface Props {
    title: string
    open: boolean
    setOpen: (open: boolean) => void
    createRow: (dto: AdmNoticeDto) => void
    updateRow: (dto: AdmNoticeDto) => void

    rowForUpdate?: AdmNoticeDto | null
}

export type InputsNotice = {
    title: string
    content: string
    start_at: string
    end_at: string
}

export default function NoticeForm(props: Props) {

    const classes = useStyles()
    const { register, handleSubmit, reset, watch, errors } = useForm<InputsNotice>()

    const [startDate, setStartDate] = React.useState<Date>(new Date())
    const [endDate, setEndDate] = React.useState<Date>(new Date())

    const handleClose = () => {
        props.setOpen(false)
    }

    const onSubmit: SubmitHandler<InputsNotice> = data => {

        console.log(`onSubmit startDate( ${startDate} ) endDate( ${endDate} )`)
        console.log('InputsNotice: ', data)

        const dto = new AdmNoticeDto()
        dto.title = data.title
        dto.content = data.content
        dto.start_at = startDate
        dto.end_at = endDate
        if (props.rowForUpdate) {
            dto.id = props.rowForUpdate.id
            props.updateRow && props.updateRow(dto)
        } else {
            props.createRow && props.createRow(dto)
        }
    }

    useEffect(()=>{
        if (props.rowForUpdate) {
            reset({ title: props.rowForUpdate.title, content: props.rowForUpdate.content })
            setStartDate(props.rowForUpdate.start_at)
            setEndDate(props.rowForUpdate.end_at)
        }
    }, [reset, props.rowForUpdate])

    return (
        <Dialog open={props.open} onClose={handleClose} aria-labelledby='form-dialog-title'>
            <form onSubmit={handleSubmit(onSubmit)}>
                <DialogTitle id='form-dialog-title'>{props.title}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                       공지사항 내용을 입력하십시오.
                    </DialogContentText>
                    <TextField margin="dense" id='title' name='title' label='제목' type='text'
                               fullWidth variant='standard' autoFocus inputRef={register({ required: true })} />
                    <TextField multiline rows={12} margin='dense' id='content' name='content' label='본문' type='text'
                               fullWidth variant='standard' inputRef={register({ required: true })} />


                               <Divider/>
                     <Typography>-</Typography>

                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <DatePicker label='시작일' value={startDate}
                                    onChange={(newValue) => {
                                        if (newValue) {

                                            console.log('startDate raw: ', newValue)
                                            console.log('toDateString raw: ',newValue.toDateString())

                                            newValue.setHours(0, 0, 0, 0)
                                            setStartDate(new Date(newValue.toDateString()))
                                        }
                                    }}
                                    renderInput={(params) => <TextField {...params} />}
                        />
                    </LocalizationProvider>

                    <LocalizationProvider dateAdapter={AdapterDateFns}>
                        <DatePicker label='종료일' value={endDate}
                                    onChange={(newValue) => {
                                        if (newValue) {
                                            newValue.setHours(0, 0, 0, 0)
                                            setEndDate(new Date(newValue.toDateString()))
                                        }
                                    }}
                                    renderInput={(params) => <TextField {...params} />}
                        />
                    </LocalizationProvider>

                </DialogContent>

                <div>
                    <Card className={classes.cardRoot}>
                    </Card>
                </div>

                <DialogActions>
                    <Button onClick={e => {
                        e.preventDefault()
                        handleClose()
                    }}>취소</Button>
                    <Button type='submit'>등록</Button>
                </DialogActions>
            </form>
        </Dialog>
    )
}
