import React from 'react'
import { SubmitHandler, useForm } from 'react-hook-form'
import { Paper, Button, TextField } from '@material-ui/core'
import { LoginDto } from '../services/dto/user.dto'
import { AdminUserApi } from '../services/adminApi'


export type InputsLogIn = {
    userid: string
    password: string
}

type Props = {
    handleLogin: Function
}

export default function LogInForm(props: Props) {

    const { register, handleSubmit, reset, watch, errors } = useForm<InputsLogIn>()

    const onSubmit: SubmitHandler<InputsLogIn> = data => {
        console.log('onSubmit: ', data)

        props.handleLogin(data.userid, data.password)
    }



    return (
        <form className='form' onSubmit={handleSubmit(onSubmit)}>
            <TextField margin='dense' id='userid' name='userid' label='이메일' type='text'
                       fullWidth variant='standard' autoFocus inputRef={register({ required: true })} />

            <TextField margin='dense' id='password' name='password' label='비밀번호' type='text'
                       fullWidth variant='standard' inputRef={register({ required: true })} />

                       <br/>
            <Button type='submit' variant='outlined' color='primary' style={{marginTop:'16px'}}>로그인</Button>
        </form>
    )
}
