import React, { useEffect, useState, createContext, FC } from 'react'
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles'
import {
    Grid, IconButton, Pagination,
    Paper,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TablePagination,
    TableRow, Toolbar, Tooltip, Checkbox, TextField, FormControl, InputLabel, Select,
    MenuItem
} from '@material-ui/core'
import { StyledTableCell, StyledTableRow } from './GenericTableStyles'
import RadioButtonUncheckedIcon from '@material-ui/icons/RadioButtonUnchecked'
import AddIcon from '@material-ui/icons/Add'
import DeleteIcon from '@material-ui/icons/Delete'
import SearchIcon from '@material-ui/icons/Search'
import ImportExportIcon from '@material-ui/icons/ImportExport'
import RcCheckbox from '../Controls/RcCheckbox'
