import { CustomRenderers, MinTableItem, objectKeys, TableHeaders, isPrimitive, objectValues } from './Common'
import EditIcon from '@material-ui/icons/Edit'
import ClearIcon from '@material-ui/icons/Clear'


interface Props<T extends MinTableItem> {
    items: T[]
    numSelected?: number
    onClickUncheckAll: () => void
    onClickAdd?: Function | undefined
    onClickEdit?: Function | undefined
    onClickDelete?: (selectedIDs: Set<number | string>) => void
    selectedRowIndices: number[]

    searchKeyword: string
    setSearchKeyword: (newValue: string) => void
    selectedIDs: Set<number | string>
}

export function GenericTableToolbar<T extends MinTableItem>(props: Props<T>) {

    const onChangeSearchKeyword = (e: React.ChangeEvent<HTMLInputElement>) => {
        props.setSearchKeyword(e.target.value)
    }

    return (
        <div>
            <Toolbar>
                <Tooltip title='Uncheck All'>
                    <IconButton size='small' edge='start'
                                onClick={() => props.onClickUncheckAll()}><RadioButtonUncheckedIcon /></IconButton>
                </Tooltip>
                <Tooltip title='추가'>
                    <IconButton size='small' onClick={() => {
                        console.log('추가 clicked ! ')
                        if (props.onClickAdd)
                            props.onClickAdd()
                    }}><AddIcon /></IconButton>
                </Tooltip>
                <Tooltip title='편집'>
                    <IconButton size='small' onClick={() => {
                        if (props.onClickEdit) {
                            if (props.selectedIDs.size === 1) {
                                const id = Array.from(props.selectedIDs)[0]
                                const found = props.items.find(elem => elem.id === id)
                                props.onClickEdit(found)
                            } else {
                                //props.onClickEdit(null)
                            }
                        }
                    }}><EditIcon /></IconButton>
                </Tooltip>
                <Tooltip title='삭제'>
                    <IconButton size='small' onClick={() => {
                        props.onClickDelete && props.onClickDelete(props.selectedIDs)
                    }}><DeleteIcon /></IconButton>
                </Tooltip>
                {/*<Tooltip title='Export'><IconButton size='small'><ImportExportIcon /></IconButton></Tooltip>*/}

                <Tooltip title='현재 페이지 검색' style={{ marginLeft: '10px', marginBottom: '14px' }}>
                    {/*<TextField id="standard-basic" label="Search this table" variant="standard" />*/}
                    <TextField size='small' label='Search in page' variant='standard'
                               value={props.searchKeyword} onChange={onChangeSearchKeyword} />
                </Tooltip>
                <Tooltip title="Clear">
                    <IconButton size="small" onClick={()=>props.setSearchKeyword('')}><ClearIcon/></IconButton>
                </Tooltip>

                {/*<Tooltip title='이 테이블 검색' style={{ marginLeft: '10px', marginBottom: '14px' }}>*/}
                {/*    <TextField size='small' label='Db 테이블 검색' variant='standard' />*/}
                {/*</Tooltip>*/}
                {/*<Tooltip title='Db 검색'>*/}
                {/*    <IconButton size='small' onClick={() => {*/}
                {/*    }}><SearchIcon /></IconButton>*/}
                {/*</Tooltip>*/}

            </Toolbar>
        </div>
    )
}