export default function VideoList() {

    const [rowsTechVideo, setRowsTechVideo] = useState([])
    const [searchKeywordInPage1, setSearchKeywordInPage1] = useState('')

    const [rowsDriveVideo, setRowsDriveVideo] = useState([])
    const [searchKeywordInPage2, setSearchKeywordInPage2] = useState('')

    const [openTechVideoForm, setOpenTechVideoForm] = useState(false)
    const [openDriveVideoForm, setOpenDriveVideoForm] = useState(false)

    const [rowForEdit1, setRowForEdit1] = useState(null)
    const [rowForEdit2, setRowForEdit2] = useState(null)

    const onClickAddTechVideo = () => {

        console.log('onClickAddTechVideo ')

        setOpenTechVideoForm(true)
    }
    const onClickAddDriveVideo = () => {

        console.log('onClickAddDriveVideo ')
        setOpenDriveVideoForm(true)
    }
    const onClickEditTech = (item: any) => {
        console.log('onClickEdit item: ', item)
        setRowForEdit1(item)
        setOpenTechVideoForm(true)
    }
    const onClickEditDrive = (item: any) => {
        console.log('onClickEdit item: ', item)
        setRowForEdit2(item)
        setOpenDriveVideoForm(true)
    }

    /// CRUD    ----------------------------------------------------------------
    const addOneVideo = (type: VideoType, dto: CreateVideoDto) => {
        //
        VideoApi.create(type, dto)
            .then(resData => {
                console.log('resData: ', resData)
                setOpenTechVideoForm(false)
                setOpenDriveVideoForm(false)
                readAll(type)
            })
            .catch(e => {
                console.warn(e)
                alert(`${dto} 데이터를 등록하지 못하였습니다.`)
                setOpenTechVideoForm(false)
                setOpenDriveVideoForm(false)
            })
    }

    const readAll = (type: VideoType, page: number = 0, limit: number = 0) => {
        VideoApi
            .getAll(type)
            .then(jsonData => {
                console.log('VideoApi.getAll : ', jsonData)
                type === VideoType.Tech ? setRowsTechVideo(jsonData.rows) : setRowsDriveVideo(jsonData.rows)
            })
            .catch(e => {
                console.error(e)
            })
    }

    const updateOneVideo = (dto: UpdateVideoDto) => {
        console.log('updateOneVideo: ', dto)
        VideoApi
            .update(dto)
            .then(resData => {
                console.log('VideoApi.getAll : ', resData)
                // dto.type === VideoType.Tech ? setRowsTechVideo(jsonData.rows) : setRowsDriveVideo(jsonData.rows)
                readAll(VideoType.Tech)
                readAll(VideoType.Drive)
            })
            .catch(e => {
                console.error(e)
            })
            .finally(() => {
                setOpenTechVideoForm(false)
                setOpenDriveVideoForm(false)
            })
    }

    const deleteMany = (type: VideoType, idArray: number[]) => {

        if (Boolean(idArray) && idArray.length > 0) {
            const dto = new DeleteManyVideosDto()
            dto.ids = idArray
            VideoApi
                .deleteMany(type, dto)
                .then(jsonData => {
                    console.log('VideoApi.deleteMany : ', jsonData)
                    readAll(type)
                })
                .catch(e => {
                    console.error(e)
                })
        } else {
            console.log('deleteMany nothing has selected.')
        }
    }

    /// CRUD End   -------------------------------------------------------------


    /// UI Handlers  -----------------------------------------------------------
    const onClickDeleteSelectedVideo1 = (selectedIDs: Set<any>) => {
        const ids = Array.from(selectedIDs)
        deleteMany(VideoType.Tech, ids)
    }

    const onClickDeleteSelectedVideo2 = (selectedIDs: Set<any>) => {
        const ids = Array.from(selectedIDs)
        deleteMany(VideoType.Drive, ids)
    }
    /// ------------------------------------------------------------------------


    useEffect(() => {
        readAll(VideoType.Tech)
        readAll(VideoType.Drive)
    }, [])

    return (
        <div>
            <Typography align='left' variant='h6'><strong>기능 영상</strong></Typography>

            <GenericTable items={rowsTechVideo} headers={tableHeader}
                          onClickCreate={onClickAddTechVideo}
                          onClickEdit={onClickEditTech}
                          onClickDelete={onClickDeleteSelectedVideo1}
            />

            <Typography align='left' variant='h6'><strong>도로주행 영상</strong></Typography>

            <GenericTable items={rowsDriveVideo} headers={tableHeader}
                          onClickCreate={onClickAddDriveVideo}
                          onClickEdit={onClickEditDrive}
                          onClickDelete={onClickDeleteSelectedVideo2}
            />

            <VideoForm title='기능 영상 추가' open={openTechVideoForm} setOpen={setOpenTechVideoForm}
                       type={VideoType.Tech}
                       addOneVideo={addOneVideo}
                       rowForEdit={rowForEdit1}
            />
            <VideoForm title='도로주행 영상 추가' open={openDriveVideoForm} setOpen={setOpenDriveVideoForm}
                       type={VideoType.Drive}
                       addOneVideo={addOneVideo}
                       updateOneVideo={updateOneVideo}
                       rowForEdit={rowForEdit2}
            />
        </div>
    )
}
