export default function AdminUserList() {

    const [rows, setRows] = useState([])

    /// CRUD    ----------------------------------------------------------------
    const readAll = () => {
        try {
            AdminUserApi
                .readAll()
                .then(resData => {
                    console.log('AdminUserList readAll rows: ', resData.rows)
                    for (let row of resData.rows) {
                        delete row.role
                        delete row.password
                        //row.examType = getExamType(row.examType)
                        row.created = moment(row.created).format('YYYY-MM-DD hh:mm')
                        row.updated = moment(row.updated).format('YYYY-MM-DD hh:mm')
                    }
                    setRows(resData.rows)
                })
                .catch(e => {
                    console.log(e)
                })
        } catch (e) {
            console.log(e)
        }
    }
    const deleteMany = (idList: number[]) => {
            if (Boolean(idList) && idList.length > 0) {
                AdminUserApi
                    .deleteMany(idList)
                    .then(resData => {
                        console.log('NoticeApi.deleteMany : ', resData)
                        readAll()
                    })
                    .catch(e => {
                        console.error(e)
                    })
            }
        }

    /// CRUD End   -------------------------------------------------------------

    const onClickDelete = (selectedIDs: Set<any>) => {
        deleteMany(Array.from(selectedIDs))
    }

    useEffect(() => {
        readAll()
    }, [])

    const menuRenderer = (item: any) => {
        console.log('menu it: ', item)
        const onClickEdit = (item: any) => {
            console.log('onClickEdit item: ', item)
        }
        return (
            <>
                <Button size='small' variant='outlined' color='primary' onClick={() => onClickEdit(item)}>
                    <EditOutlinedIcon fontSize='small' color='action' />
                </Button>
                <ActionButton onClick={() => onClickEdit(item)} variant='outlined' color='primary'>
                    <EditOutlinedIcon fontSize='small' />
                </ActionButton>

                <IconButton color='primary' aria-label='upload picture' component='span'>
                    {/*<PhotoCamera />*/}
                    <EditOutlinedIcon fontSize='large' />
                </IconButton>
                {/*<IconButton color="primary" aria-label="upload picture" component="span" >*/}
                {/*    <EditOutlinedIcon fontSize='medium'  />*/}
                {/*</IconButton>*/}
                {/*<Button size="small" variant="outlined" startIcon={<DeleteIcon />}>*/}
                {/*    Delete*/}
                {/*</Button>*/}
            </>
        )
    }

    const customImageRenderers = {
        image: (it: any) => (
            <img alt={`${it.name}`} src={it.image.url} width={it.image.width} height={it.image.height}
                 style={{ objectFit: 'cover' }}
            />
        ),
        menu: menuRenderer
    }

    return (
        <>
            <Typography variant="h6">관리자 회원</Typography>

            <GenericTable items={rows} headers={tableHeader}
                          // customRenderers={customImageRenderers}
                onClickDelete={onClickDelete}
            />
        </>
    )
}
