import * as React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import { Card } from '@material-ui/core'
import { SubmitHandler, useForm } from 'react-hook-form'
import { VideoApi } from '../../services/adminApi'
import { VideoType } from '../../services/dto/common-dto-defs'
import { CreateVideoDto, UpdateVideoDto } from '../../services/dto/video.dto'
import { useEffect } from 'react'


export type InputsVideo = {
    title: string
    link: string
}

const useStyles = makeStyles({
    cardRoot: {
        // display: 'flex',
        // flexGrow: 1,
        maxWidth: 345,
        // justifyContent: 'center'
        marginLeft: '18px'
    },
    cardMedia: {
        height: 140
        // display: 'flex',
        // justifyContent: 'center',
        // marginLeft: '20px'
    }
})

interface Props {
    title: string
    open: boolean
    setOpen: (open: boolean) => void
    type: VideoType
    addOneVideo?: (type: VideoType, dto: CreateVideoDto) => void
    updateOneVideo?: (dto: UpdateVideoDto) => void
    rowForEdit?: UpdateVideoDto | null
    //setRowForEdit?: Function
}

export default function FormDialog(props: Props) {

    const { register, handleSubmit, setValue, reset, watch, errors } = useForm<InputsVideo>(
        {
            defaultValues: {
                title: props.rowForEdit ? props.rowForEdit.title : '',
                link: props.rowForEdit ? props.rowForEdit.link : ''
            }
        })

    const classes = useStyles()

    const handleClose = () => {
        props.setOpen(false)
    }

    const handleAdapt = () => {
        console.log()
    }

    const onSubmit: SubmitHandler<InputsVideo> = data => {
        //console.log('Register onSubmit: ', data)
        if (props.rowForEdit) {
            const dto = new UpdateVideoDto()
            dto.id = props.rowForEdit.id
            dto.title = data.title
            dto.link = data.link
            dto.type = props.type
            props.updateOneVideo && props.updateOneVideo(dto)
        } else {
            const dto = new CreateVideoDto()
            dto.title = data.title
            dto.link = data.link
            dto.type = props.type
            props.addOneVideo && props.addOneVideo(props.type, dto)
        }
    }

    useEffect(() => {
        props.rowForEdit && reset(props.rowForEdit)
    }, [reset, props.rowForEdit])

    return (
        <div>
            {/*<Button variant="outlined" onClick={handleClickOpen}>*/}
            {/*    Open form dialog*/}
            {/*</Button>*/}
            <Dialog open={props.open} onClose={handleClose} aria-labelledby='form-dialog-title'>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <DialogTitle id='form-dialog-title'>{props.title}</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            제목과 영상 링크를 넣어주세요.
                        </DialogContentText>
                        <TextField margin='dense' id='title' name='title' label='제목' type='text'
                                   fullWidth variant='standard' autoFocus inputRef={register({ required: true })} />
                        <TextField margin='dense' id='link' name='link' label='영상 주소' type='text'
                                   fullWidth variant='standard' inputRef={register({ required: true })} />
                    </DialogContent>

                    <div>
                        <Card className={classes.cardRoot}>

                            {/*<CardMedia className={classes.cardMedia} image={catImage} title="Contemplative Reptile" />*/}
                            {/*<CardMedia component="video" className={classes.cardMedia}*/}
                            {/*           image={"https://www.youtube.com/embed/05nj3y5kqms"}*/}
                            {/*           title="Contemplative Reptile"*/}
                            {/*           autoPlay*/}
                            {/*/>*/}

                            {/*<CardMedia component='iframe' src={'https://www.youtube.com/embed/05nj3y5kqms'}*/}
                            {/*           allow='autoPlay' />*/}

                            {/*<CardContent>*/}
                            {/*    <Typography gutterBottom variant="h5" component="div">Lizard</Typography>*/}
                            {/*    <Typography variant="body2" color="textSecondary" component="p">*/}
                            {/*        Lizards are a widespread group of squamate reptiles, with over 6,000*/}
                            {/*        species, ranging across all continents except Antarctica*/}
                            {/*    </Typography>*/}
                            {/*</CardContent>*/}

                            {/*<CardActions>*/}
                            {/*    <Button size="small">Share</Button>*/}
                            {/*    <Button size="small">Learn More</Button>*/}
                            {/*</CardActions>*/}
                        </Card>
                    </div>

                    <DialogActions>
                        <Button onClick={e => {
                            e.preventDefault()
                            handleClose()
                        }}>취소</Button>
                        <Button type='submit'>등록</Button>
                    </DialogActions>
                </form>
            </Dialog>
        </div>
    )
}
