export class UserIdState {
  id: number
  userid: string // email, facebook-id, google-id, ...

  public assign(id: number, userid: string) {
    this.id = id
    this.userid = userid
  }

  public isValid(): boolean {
    return Boolean(this.id) && Boolean(this.userid) && this.id > 0
  }
}
